.table-free {
  background-color: rgb(43, 255, 0);
  background: rgb(43, 255, 0);
  font-weight: bold;
  font-family: 'Oswald', sans-serif !important;
}
.table-full {
  background-color: rgb(250, 2, 2);
  background: rgb(250, 2, 2));
  color: white;
  font-family: 'Oswald', sans-serif !important;
}
.table-free:hover, .table-free:focus {
  background-color: rgb(43, 255, 0);
}
.table-full:hover, .table-full:focus {
  background-color: rgb(250, 2, 2);
}
.tableButton.btn-default, .tableButton2.btn-default {
  text-shadow: none;
  font-weight: 400;
  
}
#lbl-selected, #lbl-title {
  display: block;
  background-color: rgb(250, 2, 2);
  width: 100%;
  font-weight: 600;
  text-align: left;
  padding: 15px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  margin-top: 10px;
  margin-bottom: 10px;
}
.lbl-seat {
  display: block;
  width: 100%;
  font-weight: 600;
  text-align: left;
  padding: 15px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
#circle {
	border-style: solid;
	border-color: black;
	border-radius: 12px;
	width: 25px;
}
#PaymentsContainer {
	height: 450px;
	padding: 10px;
}
#TableSelectionContainer {
	height: 600px;
	width: 100%;
	padding: 0;
	margin: 0;
	padding-top: 40px;
}
.tableButton {
	width: 80px;
	height: 80px;
	margin: 10px;
	border-radius: 50%;
	background-image: url(../Images/table.jpg);
	background-position: center;
	background-color: #ffffff;
	background-size: 100%;
	color: white;
	background-repeat: no-repeat;
}
.tableButton2 {
	width: 80px;
	height: 80px;
	margin: 10px;
	border-radius: 50%;
	background-image: url(../Images/table.jpg);
	background-position: center;
	background-color: #ffffff;
	background-size: 100%;
	color: white;
	background-repeat: no-repeat;
}
.stage {
	-webkit-text-fill-color: #ffffff;
	font-weight: 700;
	border-color: rgba(255, 174, 0, 0.952);
	background-color: rgb(255, 174, 0);
	text-align: center;
	border-style: solid;
	border-radius: 10px;
	padding: 10px;
	width: 100%;
	margin-bottom: 20px;
	margin-left: 15px;
}

